import { Component, OnDestroy, OnInit } from "@angular/core";
import { MatBottomSheet } from "@angular/material/bottom-sheet";
import { Subscription } from "rxjs";
import { tap } from "rxjs/operators";
import { BottomSheetDataComponent } from "../bottom-sheet-data/bottom-sheet-data.component";
import { ApiService } from "../services/api.service";

@Component({
  selector: "app-update",
  templateUrl: "./update.component.html",
  styleUrls: ["./update.component.scss"],
})
export class UpdateComponent implements OnInit, OnDestroy {
  public isUpdateRequired = false;
  public isCheckingUpdateRequired = true;
  public motd = "";
  public updating = false;
  private subs: Subscription[];

  constructor(
    private service: ApiService,
    private bottomSheet: MatBottomSheet
  ) {}

  ngOnInit() {
    const sub1 = this.service
      .getUpdateRequired$()
      .pipe(
        tap((result) => {
          this.isUpdateRequired = result;
          this.isCheckingUpdateRequired = false;
        })
      )
      .subscribe(
        (result) => {},
        (error) => {
          console.log(error);
        }
      );
    this.subs.push(sub1);

    const sub2 = this.service
      .getMotd$()
      .pipe(
        tap((result) => {
          this.motd = result;
        })
      )
      .subscribe(
        (result) => {},
        (error) => {
          console.log(error);
        }
      );
    this.subs.push(sub2);
  }

  ngOnDestroy() {
    this.subs.forEach((sub) => {
      sub.unsubscribe();
    });
  }

  public doUpdate() {
    this.updating = true;
    this.service.doUpdate$().subscribe(
      (result) => {
        let msg = "";
        if (result) {
          msg = `We got the update and are applying it now. We'll refresh the page, but if the update takes a long time, you may need to refresh the page yourself in about 2 minutes.`;
        } else {
          msg = `Seems like you might already be up to date. We're going to refresh the page and check again.`;
        }
        this.bottomSheet.open(BottomSheetDataComponent, {
          data: { message: msg, progress: true },
        });
        setTimeout(() => {
          this.updating = true;
          window.location.reload();
        }, 2000);
      },
      (error) => {
        console.log(error);
      }
    );
  }
}
