import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AudioSetupComponent } from './audio-setup/audio-setup.component';
import { AboutComponent } from './Help/about/about.component';
import { SupportComponent } from './Help/support/support.component';
import { HomeComponent } from './home/home.component';
import { NetworkComponent } from './network/network.component';
import { RegistrationComponent } from './registration/registration.component';
import { UpdateComponent } from './update/update.component';


const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'audio-setup', component: AudioSetupComponent },
  {
    path: 'help', children: [
      { path: '', component: SupportComponent },
      { path: 'about', component: AboutComponent },
    ]
  },
  { path: 'network', component: NetworkComponent },
  { path: 'registration', component: RegistrationComponent },
  { path: 'update', component: UpdateComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
