import { Component, Inject, OnInit } from "@angular/core";
import { AbstractControl, FormBuilder, FormGroup } from "@angular/forms";
import { MatBottomSheet } from "@angular/material/bottom-sheet";
import { MAT_DIALOG_DATA } from "@angular/material/dialog";
import { of } from "rxjs";
import { catchError } from "rxjs/operators";
import { BottomSheetDataComponent } from "../bottom-sheet-data/bottom-sheet-data.component";
import { FormValidation } from "../forms/form-validation";
import { Output } from "../models/output";
import { ApiService } from "../services/api.service";
import { ServiceResult } from "../services/service-result";

@Component({
  selector: "app-audio-setup",
  templateUrl: "./audio-setup.component.html",
  styleUrls: ["./audio-setup.component.scss"],
})
export class AudioSetupComponent implements OnInit {
  public model: Output;
  public theForm: FormGroup;
  public saving = false;

  constructor(
    private fb: FormBuilder,
    private service: ApiService,
    // public dialogRef: MatDialogRef<AudioSetupComponent>,
    private bottomSheet: MatBottomSheet,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}

  ngOnInit() {
    this.service.getAudioConfig$().subscribe(
      (result) => {
        this.createForm(result.data);
      },
      (error) => {
        console.log(error);
        this.createForm(null);
      }
    );
  }

  cancel() {
    // this.dialogRef.close();
  }

  onSubmit() {
    if (this.theForm.valid) {
      this.saving = true;
      this.service
        .putAudioConfig$(this.getFormValues())
        .pipe(
          catchError((err, caught) => {
            FormValidation.parseSubmitErrors(err, this.theForm);
            this.saving = false;
            return of(null as ServiceResult<Output>);
          })
        )
        .subscribe((result) => {
          if (result.success) {
            this.bottomSheet.open(BottomSheetDataComponent, {
              data: {
                message: "The audio settings have been updated.",
                progress: false,
              },
            });
            // this.dialogRef.close();
          } else {
            this.bottomSheet.open(BottomSheetDataComponent, {
              data: { message: result.message, progress: false },
            });
          }
          this.saving = false;
        });
    } else {
      FormValidation.validateAllFormFields(this.theForm);
    }
  }

  createForm(model?: Output): void {
    if (this.theForm) {
      this.model = model;
      this.theForm.reset(this.getModelValues());
    } else {
      if (model) {
        this.theForm = this.fb.group({
          output: model.output,
          dop: model.dop,
          volume: model.volume,
          bufferDuration: model.bufferDuration,
          maxBits: model.maxBits,
        });
        // console.log(model.xo);
        // this.setXOIndex(model.xo);
      } else {
        this.theForm = this.fb.group({
          output: 0,
          dop: false,
          volume: false,
          bufferDuration: 0.04,
          maxBits: 32,
        });
      }
      this.model = model;
    }
  }

  getFormValues(): Output {
    const formModel = this.theForm.value;
    const saveModel: Output = {
      output: formModel.output,
      dop: formModel.dop,
      volume: formModel.volume,
      bufferDuration: formModel.bufferDuration,
      maxBits: formModel.maxBits,
    };
    return saveModel;
  }

  getModelValues(): any {
    return {
      output: this.model.output,
      dop: this.model.dop,
      volume: this.model.volume,
      bufferDuration: this.model.bufferDuration,
      maxBits: this.model.maxBits,
    };
  }

  undo(): void {
    this.theForm.reset(this.model);
  }

  // Form Control Helpers
  get output(): AbstractControl {
    return this.theForm.get("output");
  }
  get dop(): AbstractControl {
    return this.theForm.get("dop");
  }
  get volume(): AbstractControl {
    return this.theForm.get("volume");
  }
  get bufferDuration(): AbstractControl {
    return this.theForm.get("bufferDuration");
  }
  get maxBits(): AbstractControl {
    return this.theForm.get("maxBits");
  }

  public onoff(value: boolean) {
    return value ? "ON" : "OFF";
  }

  public get save_disabled() {
    return this.theForm.pristine || this.saving;
  }
}
