import { KeyValue } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { of } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { BottomSheetDataComponent } from '../bottom-sheet-data/bottom-sheet-data.component';
import { FormValidation } from '../forms/form-validation';
import { NetworkType } from '../models/enum-network-type';
import { SubnetMask } from '../models/enum-subnet-mask';
import { Network } from '../models/network';
import { ApiService } from '../services/api.service';
import { ServiceResult } from '../services/service-result';

@Component({
  selector: 'app-network',
  templateUrl: './network.component.html',
  styleUrls: ['./network.component.scss']
})
export class NetworkComponent implements OnInit {

  public model: Network;
  public theForm: FormGroup;
  public masks: KeyValue<string, string>[] = [];
  public saving = false;

  get static(): boolean {
    return (this.type.value === 1);
  }

  constructor(
    private fb: FormBuilder,
    private service: ApiService,
    // public dialogRef: MatDialogRef<NetworkComponent>,
    private bottomSheet: MatBottomSheet,
    // @Inject(MAT_DIALOG_DATA) public data: any
  ) {

    const values = Object.keys(SubnetMask)
      .filter(key => !isNaN(Number(key)))
      .map(key => {
        const value = SubnetMask[key];
        // let kvp = { key, val } (KeyValue<number, string>)
        return { key, value };
      });
    this.masks = values;
  }

  ngOnInit() {
    this.service.network$().subscribe(result => {
      this.createForm(result.data);
    },
      error => {
        console.log(error);
        this.createForm(null);
      }
    );
  }

  cancel() {
    // this.dialogRef.close();
  }

  onSubmit() {
    if (this.theForm.valid) {
      this.saving = true;
      this.model = this.getFormValues();
      this.service.networkSave$(this.model)
        .pipe(
          catchError((err, caught) => {
            FormValidation.parseSubmitErrors(err, this.theForm);
            this.saving = false;
            return of(null as ServiceResult<Network>);
          })
        )
        .subscribe(result => {
          if (result.success) {
            if (this.model.type === NetworkType.DHCP) {
              const msg = `We're unable to get the new IP Address when you change to DHCP. We'll try taking you to 'mp.local', but you may need to look up the IP in your router.`;
              this.bottomSheet.open(BottomSheetDataComponent, { data: { message: msg, progress: true } });
              setTimeout(() => {
                window.location.href = 'http://mp.local';
              }, 2000);
            } else {
              if (result.data.ip !== window.location.hostname) {
                const msg = `We're updating the ip address to '${result.data.ip}'. We'll redirect you there shortly.`;
                this.bottomSheet.open(BottomSheetDataComponent, { data: { message: msg, progress: true } });
                setTimeout(() => {
                  window.location.href = 'http://' + result.data.ip;
                }, 2000);
              } else {
                this.bottomSheet
                  .open(BottomSheetDataComponent, { data: { message: 'The network settings have been updated.', progress: false } });
              }
            }
          } else {
            this.bottomSheet.open(BottomSheetDataComponent, { data: { message: result.message, progress: false } });
          }
          this.saving = false;
        });
    } else {
      FormValidation.validateAllFormFields(this.theForm);
    }
  }

  createForm(model?: Network): void {
    if (this.theForm) {
      this.model = model;
      this.theForm.reset(this.getModelValues());
    } else {
      if (model) {
        this.theForm = this.fb.group({
          ip: new FormControl({ value: model.ip, disabled: model.type === NetworkType.DHCP }),
          sm: new FormControl({ value: model.sm, disabled: model.type === NetworkType.DHCP }),
          gw: new FormControl({ value: model.gw, disabled: model.type === NetworkType.DHCP }),
          dns_1: new FormControl({ value: model.dns_1, disabled: model.type === NetworkType.DHCP }),
          dns_2: new FormControl({ value: model.dns_2, disabled: model.type === NetworkType.DHCP }),
          type: model.type
        });

        this.type.valueChanges
          .subscribe(result => {
            this.setControlState(this.type.value === NetworkType.Static);
          })
      } else {
        this.theForm = this.fb.group({
          ip: '',
          sm: '',
          gw: '',
          dns_1: '',
          dns_2: '',
          type: 0
        });
      }
      this.model = model;
    }
  }

  getFormValues(): Network {
    const formModel = this.theForm.value;
    const saveModel: Network = {
      ip: formModel.ip,
      // sm: SubnetMask[formModel.sm],
      sm: formModel.sm,
      gw: formModel.gw,
      dns_1: formModel.dns_1,
      dns_2: formModel.dns_2,
      type: formModel.type
    };
    return saveModel;
  }

  getModelValues(): Network {
    return {
      ip: this.model.ip,
      sm: this.model.sm,
      gw: this.model.gw,
      dns_1: this.model.dns_1,
      dns_2: this.model.dns_2,
      type: this.model.type
    };
  }

  undo(): void {
    this.theForm.reset(this.model);
  }

  setControlState(enable: boolean) {
    if (enable) {
      this.ip.enable();
      this.sm.enable();
      this.gw.enable();
      this.dns_1.enable();
    } else {
      this.ip.disable();
      this.sm.disable();
      this.gw.disable();
      this.dns_1.disable();
    }

  }

  // Form Control Helpers
  get ip(): AbstractControl {
    return this.theForm.get('ip');
  }
  get sm(): AbstractControl {
    return this.theForm.get('sm');
  }
  get gw(): AbstractControl {
    return this.theForm.get('gw');
  }
  get dns_1(): AbstractControl {
    return this.theForm.get('dns_1');
  }
  get dns_2(): AbstractControl {
    return this.theForm.get('dns_2');
  }
  get type(): AbstractControl {
    return this.theForm.get('type');
  }

  public get save_disabled() {
    return this.theForm.pristine || this.saving;
  }

}
