import { Component, Inject, OnInit } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { of } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { FormValidation } from '../forms/form-validation';
import { Registration } from '../models/registration';
import { ApiService } from '../services/api.service';
import { ServiceResult } from '../services/service-result';

@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.scss']
})
export class RegistrationComponent implements OnInit {

  public model: Registration;
  public theForm: FormGroup;
  public saving = false;

  constructor(
    private fb: FormBuilder,
    private service: ApiService,
    @Inject(MAT_DIALOG_DATA) public data: any) { }

  ngOnInit() {
    this.service.registration$().subscribe(result => {
      this.createForm(result.data);
    });
  }

  cancel() {
  }

  // save() {
  //   const data = new Registration();
  //   data.name = this.name;
  //   data.email = this.email;
  //   console.log("registration save");
  //   console.log(data);
  //   this.service.registrationSave$(data).subscribe(result => {
  //     console.log(result);
  //   })
  //   //TODO: notify
  //   this.dialogRef.close();
  // }

  onSubmit() {
    if (this.theForm.valid) {
      this.saving = true;
      // this.errorMessages = null;
      // this.saving$ = true;
      // this.submitted$ = true;
      this.service.registrationSave$(this.getFormValues())
        .pipe(
          catchError((err, caught) => {
            FormValidation.parseSubmitErrors(err, this.theForm);
            this.saving = false;
            // return Observable.empty();
            return of(null as ServiceResult<Registration>);
          })
        )
        .subscribe(savedData => {
          if (savedData.success) {
            console.log(savedData);
            // TODO: notify

          } else {
            console.log('.subscribe(savedData => { HAS NO DATA???');
            console.log(savedData);
          }
          this.saving = false;
        });
    } else {
      FormValidation.validateAllFormFields(this.theForm);
    }
  }

  createForm(_model?: Registration): void {
    if (this.theForm) {
      this.model = _model;
      this.theForm.reset(this.getModelValues());
    } else {
      this.theForm = this.fb.group({
        name: [_model.name, Validators.required],
        email: [_model.email, Validators.required],
        purchase_date: [{ value: _model.purchase_date, disabled: !_model.first_use }, Validators.required],
        serial_number: [{ value: _model.serial_number, disabled: !_model.first_use }, Validators.required],
        device_id: [{ value: _model.device_id, disabled: !_model.first_use }, Validators.required],
        firmware: [{ value: _model.firmware, disabled: !_model.first_use }, Validators.required],
        model: [{ value: _model.model, disabled: !_model.first_use }, Validators.required]
      });

      this.model = _model;
    }
  }

  getFormValues(): Registration {
    const formModel = this.theForm.value;
    let saveModel: Registration;
    if (this.model.first_use) {
      saveModel = {
        name: formModel.name,
        email: formModel.email,
        purchase_date: formModel.purchase_date,
        serial_number: formModel.serial_number,
        device_id: formModel.device_id,
        firmware: formModel.firmware,
        model: formModel.model,
        first_use: false
      };
    } else {
      saveModel = {
        name: formModel.name,
        email: formModel.email,
        purchase_date: this.model.purchase_date,
        serial_number: this.model.serial_number,
        device_id: this.model.device_id,
        firmware: this.model.firmware,
        model: this.model.model,
        first_use: false
      };
    }
    return saveModel;
  }

  getModelValues(): any {
    return {
      name: this.model.name,
      email: this.model.email,
      purchaseDate: this.model.purchase_date,
      serialNumber: this.model.serial_number,
      device_id: this.model.device_id,
      firmware: this.model.firmware,
      model: this.model.model,
      first_use: false

    };
  }

  undo(): void {
    this.theForm.reset(this.model);
  }

  // Form Control Helpers
  get name(): AbstractControl {
    return this.theForm.get('name');
  }
  get email(): AbstractControl {
    return this.theForm.get('email');
  }
  get purchase_date(): AbstractControl {
    return this.theForm.get('purchase_date');
  }
  get serial_number(): AbstractControl {
    return this.theForm.get('serial_number');
  }
  get device_id(): AbstractControl {
    return this.theForm.get('device_id');
  }
  get firmware(): AbstractControl {
    return this.theForm.get('firmware');
  }
  get unitmodel(): AbstractControl {
    return this.theForm.get('model');
  }

  public get save_disabled() {
    return this.theForm.pristine || this.saving;
  }

}
